@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}
.navWrapper{
    width: 100%;
    padding: 20px 0;
    border: 1px solid #E8E8E8;
    box-sizing: border-box;
    box-shadow: 0 4px 25px rgba(0, 0, 0, 0.04);
}
nav{
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 1400px;
}
nav ul{
    display: flex;
}
a{
    text-decoration: none;
    color: black;
}
li{
    list-style-image: url("assets/img/liTypeLine.svg");
    list-style-position: inside;
}
nav li{
    list-style: none;
}
nav li:first-child{
    margin-left: 10px;
}
nav li:not(:last-child){
    margin-right: 1vw;
}
nav a{
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    /* identical to box height */

    display: flex;
    align-items: center;

    /* black */

    color: #19191B;
}
.navIconsGroup img:not(:first-child){
    margin-left: 2vw;
}
.navIconsGroup{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.headerWrapper{
    background-image: url("assets/img/BackGround1.svg");
    background-size: cover;
    width: 100%;
}
header{
    margin: 0 auto;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
    max-width: 1400px;
}
.selling,.installation,.introduction{
    width: 33%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-right: 1px solid #EAEAEA;
    border-left: 1px solid #EAEAEA;
    box-sizing: border-box;
    padding: 50px 20px;
}
.selling h2,.installation h2,.introduction h2{
    padding: 20px 0;
}
.selling li,.installation li,.introduction li{
    padding: 0.25em 0;
}
.installation h2{
    padding-top: 25px;
}
.introduction h2{
    padding-top: 40px;
}
.competenciesWrapper{
    width: 100%;
}
.competencies{
    margin: 0 auto;
    width: 100%;
    max-width: 1400px;
}
.competencies h1{
    padding: 2.5em 0 1em;
    text-align: center;
    font-style: normal;
    font-weight: bold;
    font-size: 45px;
    line-height: 53px;
}
.competenciesContentWrapper{
    display: flex;
    justify-content: center;
    padding-bottom: 6em;
}
.development{
    margin: 0 2vw;
}
.analytics,.development,.support{
    width: 33.3%;
    border: 1px solid #EFEFEF;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.development img,.support img{
    margin-top: 10px;
}
.analytics div,.development div,.support div{
    width: 100%;
    height: 146px;
    align-items: center;
    text-align: center;
    background: #ECECEC;
}
.analytics h4,.development h4,.support h4{
    padding: 20px 0;
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 25px;
}
.analytics p,.development p,.support p{
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
}

.oneSWrapper{
    width: 100%;
    background-image: url("assets/img/BackGround2.svg");
    background-size: cover;
}
.oneSWrapper h1{
    width: 100%;
    background: #C7C7C7;
    text-align: center;
    font-weight: bold;
    font-size: 45px;
    line-height: 53px;
    padding: 1.25em 0;
}
.oneS{
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding-bottom: 6em;
}
.oneSContentWrapper{
    display: flex;
    justify-content: center;
    align-items: start;
}
.oneSContent:nth-child(2){
    margin: 0 2em;
}
.oneSContent{
    position: relative;
    z-index: 10;
}
.oneSContent div{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 1em;
}
.oneSContent div:not(:nth-last-child(2)){
    border-bottom: 1px solid #C7C7C7;
    box-sizing: border-box;
    border-radius: 4px;
}
.oneSContent:first-child div:nth-child(4){
    border: none;
}
.oneSContent:first-child div:not(:first-child){
    padding: 1.5em 2em;
}
.oneSContent:nth-child(2) div{
    padding: 1.5em 2em;
}
.oneSContent:nth-child(3) div{
    padding: 1.5em 2em;
}
.oneSContent div{
    padding-left: 2em;
}
.oneSContent h4:not(:first-child){
    padding-left: 1em;
}
.oneSContent:first-child div:first-child h4{
    padding-left: 0;
}
.circleOne{
    position: relative;
    z-index: 10;
}
.StripedLine{
    position: absolute;
    z-index: 1;
    bottom: 36px;
    left: 40px;
}
.weCanDoWrapper{
    width: 100%;
    background-size: cover;
    background-image: url("assets/img/BackGround3.svg");
}
.weCanDo{
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
}
.weCanDo h1{
    padding: 2.7em 0 1.35em;
    text-align: center;
    font-style: normal;
    font-weight: bold;
    font-size: 45px;
    line-height: 53px;
    color: #1E1E1E;
}
.weCanDoContentWrapper{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding-bottom: 8.2em;
}
.weCanDoContentFirst, .weCanDoContentSecond, .weCanDoContentThird{
    position: relative;
    z-index: 10;
    display: flex;
    flex: 1 1 33%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.weCanDoContentFirst h3, .weCanDoContentSecond h3, .weCanDoContentThird h3{
    padding: 2em 0 1em;
}
.weCanDoLine{
    position: absolute;
    z-index: 1;
    top: 38%;
}
